mal;\r\n}\r\n.marketing .col-lg-4 p {\r\n  margin-right: 10px;\r\n  margin-left: 10px;\r\n}\r\n\r\n\r\n/* Featurettes\r\n------------------------- */\r\n\r\n.featurette-divider {\r\n  margin: 80px 0; /* Space out the Bootstrap <hr> more */\r\n}\r\n\r\n/* Thin out the marketing headings */\r\n.featurette-heading {\r\n  font-weight: 300;\r\n  line-height: 1;\r\n  letter-spacing: -1px;\r\n}\r\n\r\n\r\n/* RESPONSIVE CSS\r\n-------------------------------------------------- */\r\n\r\n@media (min-width: 768px) {\r\n  /* Navbar positioning foo */\r\n  .navbar-wrapper {\r\n    margin-top: 20px;\r\n  }\r\n  .navbar-wrapper .container {\r\n    padding-right: 15px;\r\n    padding-left: 15px;\r\n  }\r\n  .navbar-wrapper .navbar {\r\n    padding-right: 0;\r\n    padding-left: 0;\r\n  }\r\n\r\n  /* The navbar becomes detached from the top, so we round the corners */\r\n  .navbar-wrapper .navbar {\r\n    border-radius: 4px;\r\n  }\r\n\r\n  /* Bump up size of carousel content */\r\n  .carousel-caption p {\r\n    margin-bottom: 20px;\r\n    font-size: 14px;\r\n    line-height: 1.4;\r\n  }\r\n\r\n  .featurette-heading {\r\n    font-size: 50px;\r\n  }\r\n}\r\n\r\n@media (min-width: 992px) {\r\n  .featurette-heading {\r\n    margin-top: 120px;\r\n  }\r\n}\r\n"


